import React from "react";

const WhyIsWorth = () => {
    const cards = [
        {
            ico: "fas fa-utensils",
            title: "PIZZA, ZIMNE NAPOJE, PRZEKSKI",
            text:
                "Na naszym hackathonie nie bdziesz chodzi godny! Zapewniamy kolacj, pyszne niadanie i stay dostp do zimnych napoi i przeksek."
        },
        {
            ico: "fas fa-gamepad",
            title: "CHILLROOM, GAMEROOM, SLEEPROOM",
            text:
                "Przygotowalimy dla Ciebie specjalne sale, gdzie do Twojej dyspozycji bdzie konsola, VR, ping pong, pikarzyki i o wiele wicej!"
        },
        {
            ico: "fab fa-playstation",
            title: "TURNIEJ W FIFA 20 I TEKKEN 7",
            text:
                "24 godziny cigego kodowania to kawa czasu, dlatego w ramach odpoczynku 'dla umysu' organizujemy turniej na konsoli PS4 :)"
        },
        {
            ico: "fas fa-user-tie",
            title: "POMOC MENTORW I NAUCZYCIELI",
            text:
                "Nie jeste pewny swoich umiejtnoci? Nie stresuj si! Mo偶esz liczy na pomoc dowiadczonych mentor贸w i nauczycieli."
        }
    ];

    const Card = (props) => {
        const {ico, title, text} = props;
        return (
        <div className={`why-worth__cards__box`}>
            <div className={"why-worth__cards__box__left"}>
                <div className={"why-worth__cards__box__left__title"}>{title}</div>
                <p className={"why-worth__cards__box__left__text"}>{text}</p>
            </div>
            <div className={"why-worth__cards__box__right"}>
                <i className={ico} />
            </div>
        </div>
        );
    };

    return (
        <div className="why-worth-container">
            <div className="why-worth">
                <div className="why-worth__title">
                    <span>Dlaczego warto wzi udzia?</span>
                </div>
                <div className={"why-worth__cards"}>
                    {cards.map((card, index) => {
                        const { ico, title, text } = card;
                        return <Card key={index} ico={ico} title={title} text={text} />;
                    })}
                </div>
            </div>
        </div>
    );
};

export default WhyIsWorth;
